* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.wraper{display:flex;justify-content:space-around;align-items:center;min-height:10vh}.wraper .logo a{text-decoration:none;color:#00ffff;font-size:3rem}.wraper nav{display:flex;justify-content:space-around;align-items:center}@media (max-width: 768px){.wraper nav{display:none}}.wraper nav a{list-style:none;padding:0 2rem;text-decoration:none;text-transform:uppercase;letter-spacing:5px;font-weight:600;color:black}.main{height:85vh;display:flex;align-items:center;justify-content:space-evenly;background-color:white;padding:0 2rem}.main .title{display:flex;flex-direction:column;align-items:left;margin-bottom:5rem}.main .title h2{font-size:2.5rem;margin:0;z-index:10;color:white;mix-blend-mode:difference}@media (max-width: 768px){.main .title h2{font-size:2.5rem}}@media (max-width: 500px){.main .title h2{font-size:2rem}}.main .title h2 .logo{color:red;font-weight:900;font-size:3rem}@media (max-width: 767px){.main .title h2 .logo{font-size:3rem;font-weight:900}}.main .spacer{min-width:20vw}.main .spacer-social{display:flex;justify-content:space-between;align-items:center;margin-bottom:5rem}@media (max-width: 768px){.main .spacer-social{flex-direction:column;height:25vh;align-items:flex-end}}.main .spacer-social a{color:black}.main .spacer-social a i{padding:0 1.5rem}.intro{position:absolute;top:0;left:0;width:100%;height:100%;background:black;z-index:8}.skills{display:flex;justify-content:space-around;flex-direction:column;height:85vh;align-items:center}.skills .rocking{display:flex;flex-direction:column;align-items:center;width:100%;justify-content:space-between}.skills .rocking .rocking-title{font-size:2.2rem;padding-bottom:3rem;letter-spacing:3px}.skills .rocking .icons{display:flex;justify-content:space-around;align-items:center}@media (max-width: 767px){.skills .rocking .icons{transition:all 300ms ease-in-out;display:grid;grid:"fab fab fab" "fab fab fab"}}.skills .rocking .icons i{padding:1rem 2rem}.skills .want{display:flex;flex-direction:column;align-items:center}.skills .want .want-title{font-size:2.2rem;padding-bottom:3rem;letter-spacing:3px}.skills .want .fab{padding:0 2rem}.project{display:flex;flex-direction:column;align-items:center;padding:20px 80px}.directory-menu{width:100%;display:flex;flex-wrap:wrap;justify-content:space-between}.menu-item{min-width:30%;height:240px;flex:1 1 auto;display:flex;align-items:center;justify-content:center;border:2px solid #383738;margin:0 7.5px 15px;overflow:hidden}@media (max-width: 1150px){.menu-item{min-width:100%}}.menu-item:hover .background-image{transform:scale(1.1);transition:transform 6s cubic-bezier(0.25, 0.45, 0.45, 0.95)}.menu-item:hover .content{opacity:0.9;cursor:pointer}.menu-item.large{height:380px}.menu-item:first-child{margin-right:7.5px}.menu-item:last-child{margin-left:7.5px}.menu-item .background-image{width:100%;height:100%;background-position:center;background-size:cover}.menu-item .content{height:90px;padding:0 25px;display:flex;flex-direction:column;align-items:center;text-align:center;justify-content:center;border:1px solid black;background-color:white;opacity:0.7;position:absolute}.menu-item .content a{text-decoration:none}.menu-item .content .title{font-weight:bold;margin-bottom:6px;font-size:22px;color:#4a4a4a}.menu-item .content .subtitle{font-weight:lighter;font-size:16px}.hamburger{display:flex;flex-direction:column;justify-content:space-around;align-items:center;cursor:pointer}@media (min-width: 769px){.hamburger{display:none}}.hamburger span{width:2rem;height:0.25rem;background:black;margin:0.13rem 0;cursor:pointer;border-radius:5px;transition:all 0.3s linear;transform-origin:1px}.close span:nth-child(1){transform:rotate(35deg)}.close span:nth-child(2){transform:scale(0)}.close span:nth-child(3){transform:rotate(-35deg)}.drawer{position:absolute;top:0;left:0;width:70%;height:100%;z-index:11;background:black;transform:translateX(-100%);transition:transform 300ms ease-in;display:flex;justify-content:center;align-items:center}.drawer .drawer-links ul{height:100%;justify-content:space-around;align-items:center;text-align:center}.drawer .drawer-links ul li{height:100%;padding:2rem 0}.drawer .drawer-links ul li .logo{font-size:3rem}.drawer .drawer-links ul li a{height:100%;text-decoration:none;color:white;text-transform:uppercase;padding:1rem 0;letter-spacing:3px;font-weight:600}.drawer.open{transform:translateX(-8%)}.contact{display:flex;height:80vh;justify-content:space-between;align-items:center;padding:4rem}@media (max-width: 767px){.contact{flex-direction:column}}.info{display:flex;flex-direction:column;align-items:left}@media (max-width: 767px){.info{display:none}}.info h2{font-size:2.5rem}.group{position:relative;margin:45px 0;width:50vw}@media (max-width: 767px){.group{min-width:90vw;position:absolute;top:10rem}}.group .form-input{background:none;background-color:white;color:gray;font-size:18px;padding:10px 10px 10px 5px;display:block;width:100%;border:none;border-radius:0;border-bottom:1px solid gray;margin:25px 0}.group .form-input:focus{outline:none}.group .btn{min-width:165px;width:100%;height:50px;letter-spacing:0.5px;line-height:50px;padding:0 35px 0 35px;font-size:15px;background-color:black;color:white;text-transform:uppercase;font-weight:bolder;border:none;cursor:pointer}.group .btn:hover{background-color:white;color:black;border:1px solid black}*{padding:0;margin:0;box-sizing:border-box;scroll-behavior:smooth}.container{width:1560px;min-width:1560px;margin:0 auto;height:100%}@media (max-width: 1560px){.container{width:1280px;min-width:1280px}}@media (max-width: 1366px){.container{width:1080px;min-width:1080px}}@media (max-width: 1080px){.container{width:100%;min-width:100%}}.container .row{display:flex;height:100%;padding:0 32px}@media (max-width: 425px){.container .row{padding:0 16px}}.container .v-center{align-items:center}.container .space-between{justify-content:space-between}.container-fluid{width:100%;height:100%}.container-fluid .row{display:flex;height:100%}

